import { Grow } from '@mui/material';
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';

const faqs = [
  {
    question: 'Vad innebr  artikelhantering med GDPR?',
    answer: "En viktig aspekt av GDPR r 'rtten att bli glmd', vilket innebr att individens data br raderas nr den inte lngre r ndvndig fr det ursprungliga syftet. Guardia tillmpar detta genom att flagga anvndare som inte varit aktiva under en lngre tid. Detta gr det mjligt fr administratrer att enkelt identifiera och, om ndvndigt, radera anvndarprofiler fr att upprtthlla GDPR-konformitet.",
  }, 
  {
    question: 'Kan flera person vara registrerade p samma plats?',
    answer: 'Ja! Guardia r en molnbaserad tjnst som r tillgnglig frn alla enheter med internetuppkoppling. Detta gr det mjligt fr flera anvndare att logga in och hantera samma artiklar samtidigt.',
  },
  {
    question: 'Vilka artiklar kan jag hantera?',
    answer: 'Guardia tillter alla typer av artiklar att lagras och hanteras. Det kan vara allt frn datorer till kaffemaskiner. Det r upp till dig! Guardia r det optimala verktyget fr strukturerad artikelhantering.',
  },   
  {
    question: 'Hur brjar jag anvnda Guardia?',
    answer: 'Boka ett demo med oss! Vi kommer att g igenom Guardia och hur det kan hjlpa dig och ditt fretag. Om du r intresserad av att anvnda Guardia tveka inte att kontakta oss!',
    link: '/demo',
    linkText: 'Boka demo!',
  },  
  
];

const SupportFaqs = () => {
  const [openIndex, setOpenIndex] = useState(null);
  const navigate = useNavigate();

  const toggle = index => {
    if (openIndex === index) {
      setOpenIndex(null);
    } else {
      setOpenIndex(index);
    }
  };

  const handleDemoClick = () => {
    navigate('/demo'); 
  };

  return (
    <div id="accordion-flush" data-accordion="collapse">
      {faqs.map((faq, index) => (
        <React.Fragment key={index}>
          <h2 id={`accordion-flush-heading-${index}`}>
            <button
              type="button"
              className="flex items-center justify-between w-full py-5 font-medium text-gray-500 border-b border-gray-200 dark:border-gray-700 dark:text-gray-400 gap-3"
              onClick={() => toggle(index)}
              aria-expanded={openIndex === index}
              aria-controls={`accordion-flush-body-${index}`}
            >
              <span className="bg-clip-text text-transparent bg-gradient-to-r from-indigo-200 to-indigo-500">{faq.question}</span>
              <svg
                data-accordion-icon
                className={`w-3 h-3 shrink-0 ${openIndex === index ? 'rotate-180' : ''}`}
                aria-hidden="true"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 10 6"
              >
                <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 5 5 1 1 5"/>
              </svg>
            </button>
          </h2>

          <Grow in={openIndex === index} style={{ transformOrigin: '0 0 0' }} timeout={500}>
            <div
              id={`accordion-flush-body-${index}`}
              className={`${openIndex === index ? 'block' : 'hidden'}`}
              aria-labelledby={`accordion-flush-heading-${index}`}
            >
              <div className="py-5 border-b border-gray-200 dark:border-gray-700">
                <p className="mb-2 text-gray-500 dark:text-gray-400">{faq.answer}</p>
                {faq.link === '/demo' ? (
                  <p className="text-gray-500 dark:text-gray-400">
                    <button className="text-blue-600 dark:text-blue-500 hover:underline" onClick={handleDemoClick}>
                      {faq.linkText}
                    </button>
                  </p>
                ) : (
                  <p className="text-gray-500 dark:text-gray-400">
                    <a href={faq.link} className="text-blue-600 dark:text-blue-500 hover:underline" target="_blank" rel="noopener noreferrer">
                      {faq.linkText}
                    </a>
                  </p>
                )}
              </div>
            </div>
          </Grow>
        </React.Fragment>
      ))}
    </div>
  );
};

export default SupportFaqs;